import React, {Fragment} from 'react';
import styled from 'styled-components';
import {Helmet} from "react-helmet";
import {Link} from "react-router-dom";

const location_titles = {
    "/": "About Pixel Tana",
    "/callToAction": "My Appeal",
    "/illustrations": "Illustrations",
    "/models": "3d Models",
    "/pixelart": "Pixel Art",
    "/miniatures": "Painted Miniatures"
}

export const HeaderStyled = styled.section`
h1{
    max-width: 100vw;
    background: linear-gradient(0deg, #C5DB94ff 60%, #68894Daa 100%);
    a {
        margin-right: 3rem;
    }
    margin: 0;
    box-shadow: 4px 4px 5px 0px #2e251e80;
    font-size: 2rem;
    padding: 0.6rem;
}
`

export default function({location}){
    let loc_title = location_titles[location.pathname]
    return (
        <HeaderStyled>
            <Helmet>
                <title>{loc_title}</title>
            </Helmet>
        <h1>
            {location.pathname !== "/" ? <Link to="/">&#171;</Link> : <Fragment/>}
            {loc_title}
        </h1>
        </HeaderStyled>
    )
}