import React from 'react';
import styled from 'styled-components';

const GalleryStyles = styled.section`
width: 100%;
min-height: 100vh;
margin: 0;
margin-top: 2rem;
overflow-y: scroll;

.GalleryImages {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
}

`

export const GalleryPage = ({children}) =>
    <GalleryStyles>
        <section className="GalleryImages">
            {children}
        </section>
    </GalleryStyles>